html, body, #root {
  height: 100%; }

.App {
  text-align: center;
  background: black;
  color: white;
  height: 100%; }

.App.timerActive {
  background: #f7f7f7; }

.App-logo {
  height: 80px; }

.App-logo.animate {
  -webkit-animation: App-logo-spin infinite 1s linear;
          animation: App-logo-spin infinite 1s linear; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.App-header {
  background-color: #3b5998;
  height: 150px;
  padding: 20px;
  color: white; }

.App-intro {
  font-size: 15em;
  margin: 0;
  font-family: Courier; }

.App-intro.down {
  color: red; }

.App-intro.down.ready {
  color: green; }

.App.timerActive .App-intro,
.App.timerActive .timelist {
  color: black; }

@media only screen and (max-device-width: 667px) {
  html, body, #root {
    width: 100%;
    height: 100%; }
  h2 {
    font-size: 15px;
    margin: 0; }
  .App-logo {
    height: 40px; }
  .App-header {
    height: 80px; }
  .App-intro {
    font-size: 4em; } }



